import React, { FC, useRef } from 'react';
import { Alert, Form, Button, message } from 'antd';
import UploadImage from 'components/biz/uploadImage';
import Editor, { ReactQuillInstance } from 'components/biz/editor';
import { UploadProps } from 'antd/lib/upload';
import styles from './index.module.scss';
const fileList: UploadProps['fileList'] = [
  {
    uid: '-1',
    url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
    name: '-1',
  },
];
const CustomForm: FC = () => {
  const editorRef = useRef<ReactQuillInstance>();
  const onFinish = (v) => {
    message.success(JSON.stringify(v));
    console.log(v, 'vvv---');
  };
  return (
    <div>
      <Alert
        message={
          <>
            <div>
               Form
              
            </div>
            <div style={{ fontWeight: 700 }}>
              -  value  valuePropName 
            </div>
            <div style={{ fontWeight: 700 }}>
              -  onChange  trigger 
            </div>
          </>
        }
        type='info'
        showIcon
      />
      <Form
        onFinish={onFinish}
        scrollToFirstError
        autoComplete='off'
        className={styles['form-container']}
        initialValues={{
          imgList: fileList,
          editText: 'busyzz',
        }}
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 18 }}
      >
        <Form.Item
          name='imgList'
          label=''
          required
          rules={[
            {
              validator(_, value) {
                if (value.length <= 0) {
                  return Promise.reject(new Error(''));
                }
                for (let i = 0; i < value.length; i++) {
                  if (value[i]?.status === 'error') {
                    return Promise.reject(
                      new Error(`${value[i]?.name} `)
                    );
                  }
                }
                return Promise.resolve();
              },
            },
          ]}
        >
          <UploadImage />
        </Form.Item>
        <Form.Item
          name='editText'
          label=''
          required
          rules={[
            {
              validator() {
                const editor = editorRef.current.getEditor();
                const unprivilegedEditor =
                  editorRef.current.makeUnprivilegedEditor(editor);
                const text = unprivilegedEditor.getText() || '';
                if (!text.trim()) {
                  return Promise.reject(new Error(''));
                }
                return Promise.resolve();
              },
            },
          ]}
        >
          <Editor ref={editorRef} />
        </Form.Item>
        <Form.Item wrapperCol={{ offset: 4 }}>
          <Button type='primary' htmlType='submit'>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default CustomForm;
